.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1e2a38;
  color: #ffffff;
  padding: 12px 20px;
  height: 60px;
}

.navbar-title {
  font-size: 18px;
  font-weight: 600;
}

.navbar-user {
  display: flex;
  align-items: center;
  font-size: 14px;
}
