.dashboard-container {
  padding: 20px;
}

.dashboard-heading {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 600;
}

.dashboard {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
}

.dashboard-card {
  background-color: #ffffff;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.dashboard-card h3 {
  margin-bottom: 10px;
  font-size: 18px;
}

.avg-value,
.percent-value {
  font-size: 24px;
  color: #333;
  font-weight: bold;
}

.dashboard-btn {
  padding: 8px 14px;
  border: none;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
}

.view-btn {
  background-color: #007bff;
  color: #fff;
}

.enroll-btn {
  background-color: #28a745;
  color: #fff;
}

.dashboard-btn:hover {
  opacity: 0.9;
}
