.sidebar {
  width: 220px;
  background-color: #f4f4f4;
  padding: 20px;
  min-height: 100vh;
}

.sidebar-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
}

.sidebar-subtitle {
  margin-top: 25px;
  font-size: 16px;
  font-weight: 500;
  color: #444;
}

.menu-list {
  list-style: none;
  padding: 0;
}

.menu-item {
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.menu-item input {
  cursor: default;
}

.menu-item a {
  text-decoration: none;
  color: #333;
}

.menu-item a.active {
  font-weight: bold;
  color: #007bff;
}
